
import Clocker from 'vux/src/components/clocker/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import Scroll from '../../../base/scroll/scroll'
import Binsert from '../../../base/bottom_insert/binsert.vue'
import Pay from '../../../base/pay/pay'
import { mapGetters } from 'vuex'
export default {
components: {
Clocker,
Cell,
Group,
Swiper,
Scroll,
Spinner,
Binsert,
Confirm,
Group,
XSwitch,
Alert,
Pay
},
name: "details",
data() {
return {
objDeli: {
leftTime: '2017-10-10 21:54:55',
},
demo07_list: [{
url: 'javascript:',
img: '',
}],
hei: '32rem',
mytype: 1,
leastNum: 3,
timeShow: true,
pic: false,
wait: false,
quan: false,
pay: false,
cg: false,
sb: false,
vip: false,
jinbi: false,
process_1: true,
process_2: true,

wran: false,
phone: false,
weixin: false,
taobao: true,
yes: '使用',
no: '不使用',
go: '前去完成',
tan: '我已支付',
text1: '正在提交订单',
alerts: '立即去绑定',

paytype: 2,
isChecked: 0,
hasmoney: 2,
title: '选择套餐',
moneyBox: [
{
price: '20',
type: '1个月套餐',
info: '每天仅需0.6元'
},
{
price: '96',
oldPrice: '120',
type: '1个月套餐',
info: '每天仅需0.5元'
},
{
price: '120',
oldPrice: '240',
type: '1个月套餐',
info: '每天仅需0.3元'
},
],
moneyIndex: 0,
type: 0,
chosed: true,



paytype1: 2,
isChecked1: 0,
hasmoney1: 2,
title1: '选择面额',
moneyBox1: [
{
price: '20金币',
oldPrice: '50元',
},
{
price: '100金币',
oldPrice: '100元',

},
{
price: '200金币',
oldPrice: '200元',
},
{
price: '300金币',
oldPrice: '300元',
},
{
price: '500金币',
oldPrice: '500元',
},
],
moneyIndex1: 0,
type1: 1,
chosed1: true,
}
},
methods: {
btnClick() {

this.pic = false
this.wait = false
this.pay = false
this.cg = false
this.sb = false
this.vip = false
this.jinbi = false
this.quan = false
this.wran = false

let that = this
this.$axios.post('/api/user/loginOrNot', {}).then((res) => {
if (res.data.code !== '200') {
this.$vux.alert.show({
title: '温馨提示',
content: '您未登录，请登录',
onHide() {
that.$router.push({ name: 'login' })
}
})
}
})

var buyerUserId = JSON.parse(window.localStorage.getItem('__userInfo__')).buyerUserId

this.$axios.post('/api/orderOperate/getFirstOrder', {
sellerTaskId: that.$route.query.sellerTaskId,
buyerUserId: buyerUserId
}).then((data) => {

if (data.data.code == 200) {
that.$router.push({ name: 'taskOneStep1', query: { buyerTaskRecordId: data.data.data.buyerTaskRecordId, sellerTaskId: that.$route.query.sellerTaskId, type: data.data.data.taskId } })
}
})
},
choosePay(index) {
this.moneyIndex = index
},
isChosed(val) {
this.chosed = val
},
choosePay1(index) {
this.moneyIndex1 = index
},
isChosed1(val) {
this.chosed1 = val
},
ceshi() {
this.cg = true
},

getDesc() {
let that = this
this.$axios.post('/api/homePage/getDesc', {
sellerTaskId: this.sellerTaskId
}).then((data) => {
console.log(data)
let res = data.data;
if (res.code == "200") {
let obj = {
picUrl: res.data.picUrl,
productName: res.data.productName,
totalNum: res.data.totalNum,
applyNum: res.data.applyNum,
leftTime: res.data.leftTime,
format: res.data.format,
price: res.data.price,
isPlus: res.data.isPlus,
isMore: res.data.isMore,
extraGold: res.data.extraGold,
isGold: res.data.isGold,
goldNum: res.data.goldNum,
leftNumToday: res.data.leftNumToday
}
that.objDeli = obj
that.demo07_list[0].img = obj.picUrl
that.leastNum = obj.leftNumToday
if (that.leastNum <= 0) {
that.mytype = 1
} else if (obj.totalNum == obj.applyNum) {
that.mytype = 1
} else {
that.mytype = 1
}
}
that.$nextTick(() => {
that.$refs.scroll.refresh()
})
}).catch((error) => {
this.$vux.alert.show({
title: '错误提示',
content: '服务器错误',
})
})
}
},
computed: {
...mapGetters([
'userNopay'
]),
ataloCount: {

get() {
let ataloCount = 0
let choosePrice = parseFloat(this.moneyBox1[this.moneyIndex1].price)
ataloCount = choosePrice - (this.chosed1 ? this.hasmoney1 : 0)
return ataloCount
},

get() {
let ataloCount = 0
let choosePrice = parseFloat(this.moneyBox[this.moneyIndex].price)
ataloCount = choosePrice - (this.chosed ? this.hasmoney : 0)
return ataloCount
},
set(val) {
return val
}
}
},









created() {
this.sellerTaskId = this.$route.query.sellerTaskId
this.getDesc()
}
}



